.staff__block{
    padding-bottom: 61px;
    margin-bottom: 61px;
    border-bottom: 1px solid rgba($color: #fff, $alpha: 0.5);
}
.staff__block:last-child{
    border-bottom: 0;
    margin-bottom: 0;
    padding-bottom: 0;
}
.staff__title{
    font-size: 20px;
    line-height: 24px;
    font-weight: bold;
    color: #fff;
    margin-bottom: 22px;
}
.staff__list{
    display: flex;
    flex-wrap: wrap;
    margin: 0 -20px;
}
.staff__item{
    width: calc(100% / 3 - 40px);
    margin: 0 20px;
    height: 551px;
    background-size: cover;
    background-position: center;
    position: relative;
}
.staff__item:before{
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(180deg, #000000 0%, rgba(196, 196, 196, 0) 100%);
    opacity: 0.3;
    transform: rotate(180deg);
}
.staff__item__bot{
    position: absolute;
    bottom: 21px;
    left: 18px;
    right: 18px;
    word-wrap: break-word;
}

.staff__name{
    font-size: 24px;
    color: #fff;
    margin-bottom: 19px;
    line-height: 1;
}
.staff__des{
    color: rgba($color: #fff, $alpha: 0.7)
}   

@media(max-width: 1300px){
    .staff__item{
        height: 450px;
    }
}
@media(max-width: 1099px){
    .staff__item{
        height: 400px;
    }
}

@media(max-width: 991px){
    .staff__item{
        width: calc(100% / 2 - 40px);
        margin-bottom: 40px;
    }
    .staff__block{
        padding-bottom: 20px;
        margin-bottom: 20px;
    }
}

@media(max-width: 767px){
    .staff__item{
        height: 350px;
        width: calc(100% / 2 - 20px);
        margin-right: 10px;
        margin-left: 10px;
        margin-bottom: 20px;
    }
    .staff__list{
        margin-left: -10px;
        margin-right: -10px;
    }
    .staff__des{
        font-size: 14px;
    }
    .staff__name{
        font-size: 20px;
    }
}
@media(max-width: 574px){
    .staff__item{
        width: calc(100% / 1 - 20px);
        height: 110vw;
    }
    .staff__name{
        font-size: 24px;
    }
}